import React, { useState } from 'react';
import './OutPuts.css';

const OutPuts=(props)=>{
    const go_to_setting=()=>{
        props.changePage('setting')
    }
    const [marginLo1,setMarginLo1]=useState('1');
    const [marginLo2,setMarginLo2]=useState('1');
    const [stateTextO1,setStateTextO1]=useState('on');
    const [stateTextO2,setStateTextO2]=useState('on');
    const bgColorOn='#f20b36';
    const bgColorOff='#6dcb01';

    const change_state=(event)=>{
        console.log('request for change state output')
        console.log(event.target.id)
        if(event.target.id=='o1'){
            if(marginLo1=='1'){
                setMarginLo1('0');
                setStateTextO1('off');
            }else{
                setMarginLo1('1');
                setStateTextO1('on');
            }
            
        }else{
            if(marginLo2=='1'){
                setMarginLo2('0');
                setStateTextO2('off');
            }else{
                setMarginLo2('1');
                setStateTextO2('on');
            }
            
        }

    }
    
    return(
        <div className="OutPuts">
            <div className="blure">
            <div className="header-Setting">
                <div className="backPart-Setting" onClick={go_to_setting}>  
                </div>
                <div className="refresh">
                <div className="icon-refresh">

                </div>
                <div className="text-refresh">
                    <p>بروز رسانی</p>

                </div>
            </div>
            </div>

            <div className="part-OutPuts" id='part1-OutPuts'>
                {
                    props.data.map(item=>{
                        return(
                            <div className="item-OutPuts" key={item.key}>
                                <div className="state-OutPuts">
                                    <div className="inputPart-OutPuts" id={item.key} onClick={change_state} style={{backgroundColor:item.key=='o1' ? (marginLo1=='1' ? (bgColorOn):(bgColorOff)):(marginLo2=='1' ? (bgColorOn):(bgColorOff))}}>
                                        <span id={item.key}>{item.key=='o1' ? stateTextO1:stateTextO2} </span>
                                        <div className="input-OutPuts" id={item.key} style={{order:(item.key=='o1' ? marginLo1 : marginLo2)}}>

                                        </div>
                                    </div>

                                </div>
                                <div className="name-OutPuts" >                                    
                                    <span style={{marginRight:'5px'}}><p> {item.state} </p></span>
                                    <span><p>    :{item.name}</p></span>
                                </div>
                                
                            </div>
                        )
                    })
                }

            </div>
            </div>

        </div>
    )
}

export default OutPuts;