import React, { useState } from 'react';
import './Page1.css';
import Device from  './Device';
// import { Zoom } from 'react-slideshow-image';
// import Carousel from 'react-multi-carousel';
// import {SlideShow} from 'slide-show-component';
// import Carousel from 'react-multi-carousel';
import 'react-multi-carousel/lib/styles.css';
import Swiper,{Navigation, Pagination} from 'swiper';
import 'swiper/css';
import 'swiper/css/navigation';
import 'swiper/css/pagination';
import 'swiper/css';
import AddDevice from'./AddDevice';



const Page1=(props)=>{
    const[addDevice,setAddDevice]=useState(false);
    const add_device=()=>{
        setAddDevice(true);

    }
    const close_add=()=>{
        setAddDevice(false)
    }
    const swiper = new Swiper('.swiper',{modules: [Navigation, Pagination]}, {
        // Optional parameters
        direction: 'vertical',
        loop: true,
      
        // If we need pagination
        pagination: {
          el: '.swiper-pagination',
        },
      
        // Navigation arrows
        navigation: {
          nextEl: '.swiper-button-next',
          prevEl: '.swiper-button-prev',
        },
      
        // And if we need scrollbar
        scrollbar: {
          el: '.swiper-scrollbar',
        },
      });
    const devices=['device1','device2','device3','device4'];
    const responsive = {
        desktop: {
          breakpoint: { max: 3000, min: 1024 },
          items: 3,
          slidesToSlide: 3 // optional, default to 1.
        },
        tablet: {
          breakpoint: { max: 1024, min: 464 },
          items: 2,
          slidesToSlide: 2 // optional, default to 1.
        },
        mobile: {
          breakpoint: { max: 464, min: 0 },
          items: 1,
          slidesToSlide: 1 // optional, default to 1.
        }
      };
    return(
        <div className="Page1">
            {addDevice ? <AddDevice close_add_device={close_add}></AddDevice>:''}
            <div className="part1-Page1 swiper">
                <div className="swiper-wrapper">

                     {
                     devices.map((item) => {
                        return (<Device key={item} changePage={props.changePage}></Device>)
                    })
                    }
                    <div className="addDevice-Page1 swiper-slide" onClick={add_device}>
                        <div className="contentAdd-Page1">
                            <div className="centerAdd-Page1">
                                <div className="iconAdd-Page1">

                                </div>
                                <div className="textAdd-Page1">
                                    <p style={{fontSize:'medium',fontWeight:'600'}}>اضافه کردن دستگاه</p>

                                </div>
                            </div>

                        </div>
 
                    </div>
                </div>
                <div className="swiper-pagination"></div>
                {/* <div className="swiper-button-prev"></div> */}
                {/* <div className="swiper-button-next"></div> */}
                <div className="swiper-scrollbar"></div>
            </div>
            <div className="part2-Page1">

                <div className="battery-Page1">
                    <span className='icons-Page1'><div className='icon-Page1' id='icon1-Page'></div></span>
                    <span className='text-Page1'>
                        <div className="title-Page1">
                            <p>باتری</p>
                        </div>
                        <div className="description-Page1">
                            <p>غیر فال می باشد</p>
                        </div>
                    </span>
                </div>

                <div className="active-Page1">
                <span className='icons-Page1'><div className='icon-Page1' id='icon2-Page'></div></span>
                    <span className='text-Page1'>
                        <div className="title-Page1">
                            <p>غیر فعال</p>
                        </div>
                        <div className="description-Page1">
                            <p>دستگاه خاموش می باشد</p>
                        </div>
                    </span>

                </div>
                <div className="ConnectTel-Page1">
                <span className='icons-Page1'><div className='icon-Page1' id='icon3-Page'></div></span>
                    <span className='text-Page1'>
                        <div className="title-Page1">
                            <p>قطع</p>
                        </div>
                        <div className="description-Page1">
                            <p>تلفن قطع می باشد</p>
                        </div>
                    </span>

                </div>
                <div className="alarm-Page1">
                <span className='icons-Page1'><div className='icon-Page1' id='icon4-Page'></div></span>
                    <span className='text-Page1'>
                        <div className="title-Page1">
                            <p>بدون آژیر</p>
                        </div>
                        <div className="description-Page1">
                            <p>آژیر فعال نمی باشد</p>
                        </div>
                    </span>

                </div>
                <div className="notif-Page1">
                <span className='icons-Page1'><div className='icon-Page1' id='icon5-Page'></div></span>
                    <span className='text-Page1'>
                        <div className="title-Page1">
                            <p>قطع</p>
                        </div>
                        <div className="description-Page1">
                            <p>هشدار فعال نمی باشد</p>
                        </div>
                    </span>

                </div>
                <div className="connect-Page1">
                <span className='icons-Page1'><div className='icon-Page1' id='icon6-Page'></div></span>
                    <span className='text-Page1'>
                        <div className="title-Page1">
                            <p>قطع</p>
                        </div>
                        <div className="description-Page1">
                            <p>اتصال هنوز برقرار نشده</p>
                        </div>
                    </span>
                    
                </div>



            </div>
            <div className="part3-Page1">

            </div>

        </div>
    )
}
export default Page1;