import React from "react";
import './Charge.css';

const Charge=(props)=>{
    const go_to_setting=()=>{
        props.changePage('setting')
    }
    return (
        <div className="Charge">
            <div className="blure">
            <div className="header-Setting">
                <div className="backPart-Setting" onClick={go_to_setting}>
                </div>
            </div>
            <div id="part1-Charge">
                <div className="cach-Charge">
                    <p>موجودي</p>
                    <p><b style={{fontWeight:'600px',fontSize:'larger'}}>{props.data}</b></p>
                    <p>ريال</p>

                </div>
                <p className="tell-Charge">كاربر گرامي لطفا جهت افزايش شارژ رمز شار دريافتي را بدون كد پيشوند يا پسوند, در كادر زير وارد كنيد  </p>
                <div className="increase-Charge">
                    <div className="submitPart-Charge">
                        <button>افزايش اعتبار</button>
                    </div>
                    <div className="inputPart-Charge">
                        <input type="text" />

                    </div>
                    

                </div>
            </div>
            </div>

        </div>
    )
}

export default Charge;