import React, { useState } from "react";
import './Remotes.css';
import RemoteAlert from "./RemoteAlert";

const Remotes=(props)=>{
    const go_to_setting=()=>{
        props.changePage('setting')
    }
    const [remoteAlert,setRemoteAlert]=useState(false);
    const change_alert_remote=()=>{
        setRemoteAlert(!remoteAlert)

    }
    return(
        
        <div className="Remotes">
            {remoteAlert ? <RemoteAlert change_alert={change_alert_remote}></RemoteAlert> :''}
            <div className="blure">
            <div className="header-Setting">
                <div className="backPart-Setting" onClick={go_to_setting}>  
            </div>

            <div className="refresh">
                <div className="icon-refresh">

                </div>
                <div className="text-refresh">
                    <p>بروز رسانی</p>

                </div>
            </div>

            </div>
            <div className="part-Remotes" id='part1-Remotes'>

            </div>
            <div className="part-Remotes" id='part2-Remotes'>
                <div className="table-Remotes">
                    <div className="title-Remotes">
                    <div className="state-Remotes">
                            <p>عمليات</p>

                        </div>
                    <div className="type-Remotes">
                            <p>نوع دسترسی</p>

                        </div>
                        <div className="name-Remotes">
                            <p>نام</p>

                        </div>
                        <div className="number-Remotes">
                            <p>شماره ریموت</p>

                        </div>
                    </div>
                    <div className="items-Remotes">
                        {
                            props.data.map(item=>{
                                return(
                                    <div className="item-Remotes">
                                        <div className="state-Remotes" onClick={change_alert_remote}>
                                            
                                            <p>ويرايش</p>
                                            

                                        </div>
                                        <div className="state-Remotes" onClick={change_alert_remote}>
                                            
                                            <p>حذف</p>
                                            

                                        </div>
                                        <div className="type-Remotes">
                                            <p>{item.type}</p>
                                            
                                        </div>
                                        <div className="name-Remotes">
                                            <p>{item.name}</p>
                                            
                                        </div>

                                         <div className="number-Remotes">
                                            <p>{item.number}</p>
                                            
                                        </div>
                                            

                                    </div>
                                )
                            })
                            
                        }

                    </div>
                    <div className="add-Remotes">
                        <div className="addIcon-Remotes">

                        </div>

                    </div>

                </div>
                
            </div>
            </div>

        </div>
    )
}

export default Remotes;