import React from "react";
import './ContactAlert.css';

const ContactAlert=(props)=>{
    return(
        <div className="ContactAlert">
            <div className="alert-ContactAlert">
                <div className="part-ContactAlert" id="part1-ContactAlert">
                    <div className="title-ContactAlert">
                        <p>ذخیره شماره جدید</p>
                    </div>
                    



                </div>
                <div className="part-ContactAlert" id="part2-ContactAlert">

                    <div className="inputPart-ContactAlert">
                        <input type="numberic" />
                    </div>

                </div>
                <div className="part-ContactAlert" id="part3-ContactAlert">
                    <div className="controlers-ContactAlert">
                        <div className="send-ContactAlert">
                            <p>ذخیره</p>


                        </div>
                        <div className="cancle-ContactAlert" onClick={props.close_alert}>
                            <p>انصراف</p>
                            
                        </div>
                    </div>

                </div>
                </div>

        </div>
    )
}

export default ContactAlert;