import React, { useState } from 'react';
import './Contacts.css';
import Contact from './Contact';
import ContactAlert from './ContactAlert';

const Contacts=(props)=>{
    const [Alert,setAlert]=useState(false);
    const close_contact_alert=()=>{
        setAlert(false)
    }
    const go_to_setting=()=>{
        props.changePage('setting')
    }
    const contact_alert=()=>{
        setAlert(true)

    }
    const contactsList=[{'storage':'5','number':'09125864785','type':'cs'},{'storage':'5','number':'09125864785','type':'cs'},{'storage':'5','number':'09125864785','type':'cs'},{'storage':'5','number':'09125864785','type':'cs'},{'storage':'5','number':'09125864785','type':'cs'}]
    return(
        <div className="Contacts">
            <div className="blure">
            <div className="header-Setting">
                <div className="backPart-Setting" onClick={go_to_setting}>
                    

            </div>

            <div className="refresh">
                <div className="icon-refresh">

                </div>
                <div className="text-refresh">
                    <p>بروز رسانی</p>

                </div>
            </div>

            </div>
            <div className="part-Contacts" id='part1-Contacts'>
                {
                    contactsList.map(contact=>{
                        return <Contact data={contact}></Contact>
                    })
                }

            </div>
            
                <div className="addIcon-Contacts" onClick={contact_alert}>
                    
                    
                </div>
            
                </div>


                {Alert ?<ContactAlert close_alert={close_contact_alert}></ContactAlert>:''}
        </div>
    )

}

export default Contacts;