import React, { useState } from 'react';
import './AddDevice.css'

const AddDevice=(props)=>{
    const[selectModel,setSelectModel]=useState(false);
    const[selectAccess,setSelectAccess]=useState(false);
    const[selectPort,setSelectPort]=useState(false);
    const change_select_model=()=>{
        setSelectModel(!selectModel);
    }
    const change_select_access=()=>{
        setSelectAccess(!selectAccess);
    }
    const change_select_port=()=>{
        setSelectPort(!selectPort);
    }
    return(
        <div className="AddDevice">
            <div className="centerPart-AddDevice">
                <div className="titl-AddDevice">

                    <p style={{fontSize:'large',fontWeight:'600'}}>اطلاعات دستگاه </p>

                </div>
                <div className="inputs-AddDevice">

                    <div className="part1Inp-AddDevice">
                        <span className='titlInp-AddDevice' style={{fontSize:'16pt'}}>مدل دستگاه</span>
                        <span className='selectModel-AddDevice'>
                            <div onClick={change_select_model}><span className='moreIcon-AddDevice'></span><span className='selected-AddDevice'>B01w</span></div>
                                <ul style={{height:selectModel ? '':'0px'}}>
                                    <li>ٍEC020</li>
                                    <li>AS20</li>
                                    <li>AlO21</li>
                                    <li>AlO21 PT</li>
                            </ul>
                        </span>
                    </div>

                    <div className="part5Inp-AddDevice">
                        <span className='selectModel-AddDevice'>
                            <div onClick={change_select_access}><span className='moreIcon-AddDevice'></span><span className='selected-AddDevice'>B01w</span></div>
                                <ul style={{height:selectAccess ? '':'0px'}}>
                                    <li>ٍEC020</li>
                                    <li>AS20</li>
                                    <li>AlO21</li>
                                    <li>AlO21 PT</li>
                            </ul>
                        </span>
                        <span className='titlInp-AddDevice' style={{fontSize:'16pt'}}>دسترسی</span>
                    </div>
                    <div className="part6Inp-AddDevice">
                        <span className='selectModel-AddDevice'>
                            <div onClick={change_select_port}><span className='moreIcon-AddDevice'></span><span className='selected-AddDevice'>B01w</span></div>
                                <ul style={{height:selectPort ? '':'0px'}}>
                                    <li>ٍEC020</li>
                                    <li>AS20</li>
                                    <li>AlO21</li>
                                    <li>AlO21 PT</li>
                            </ul>
                        </span>
                        <span className='titlInp-AddDevice' style={{fontSize:'16pt'}}>خروجی</span>
                    </div>

                    <div className="part2Inp-AddDevice">
                    <span><input type="text" /></span>
                        <span style={{fontSize:'16pt'}} className='titlInp-AddDevice'>نام دستگاه</span>
                        
                    </div>
                    <div className="part3Inp-AddDevice">
                        <span><input type="text" /></span>
                        <span style={{fontSize:'16pt'}} className='titlInp-AddDevice'>شماره سیم کارت</span>
                    </div>
                    <div className="part4Inp-AddDevice">
                        <span><input type="text" /></span>
                        <span style={{fontSize:'16pt'}} className='titlInp-AddDevice'>رمز مدیر</span>
                    </div>
                    


                </div>
                <div className="controller-AddDevice">
                    <button>تایید</button>
                    <button onClick={props.close_add_device}>انصراف</button>

                </div>
            </div>

        </div>
    )
}

export default AddDevice;