import React from 'react';
import './Device.css';

const Device=(props)=>{
    const go_to_setting=()=>{
        props.changePage('setting');
        console.log('go to setting page')
    }

    
    return(
        <div className="Device swiper-slide">
            
            
            <div className="part1-Device">
                <div className="setting-Device" onClick={go_to_setting}>
                    
                </div>
                <div className="logo-Device">
                    <img src="" alt="" />
                </div>

            </div>
            <div className="part2-Device">
                <div className="controller-Device">
                    <span className='controllerBtns-Device' id='controllerBtn1-Device'><div className="icon-Device" id='icon1-Device'></div><div className="text-Device"><p>قطع اتصال </p></div></span>
                    <span className='controllerBtns-Device' id='controllerBtn2-Device'><div className="icon-Device" id='icon2-Device'></div><div className="text-Device"><p>غیر فعال</p></div></span>
                    <span className='controllerBtns-Device' id='controllerBtn3-Device'><div className="icon-Device" id='icon3-Device'></div><div className="text-Device"><p>نیمه فعال</p></div></span>
                    <span className='controllerBtns-Device' id='controllerBtn4-Device'><div className="icon-Device" id='icon4-Device'></div><div className="text-Device"><p>فعال</p></div></span>
                </div>
            </div>

        </div>
    )
}

export default Device;