import React, { useState } from 'react';
import './ZoonInfo.css';
import ZoonAlert from './ZoonAlert'

const ZoonInfo=(props)=>{
    const[Alert,setAlert]=useState(false)
    const go_to_setting=()=>{
        props.changePage('setting')

    }
    const colse_alert=()=>{
        setAlert(false);
    }
    const zoon_alert=()=>{
        setAlert(true)

    }
    return(
        <div className="ZoonInfo">
            <div className="blure">
            <div className="header-Setting">
                <div className="backPart-Setting" onClick={go_to_setting}>  
            </div>

            <div className="refresh">
                <div className="icon-refresh">

                </div>
                <div className="text-refresh">
                    <p>بروز رسانی</p>

                </div>
            </div>

            </div>
            <div className="part-ZoonInfo" id='part1-ZoonInfo'>

            </div>
            <div className="part-ZoonInfo" id='part2-ZoonInfo'>
                <div className="table-ZoonInfo">
                    <div className="title-ZoonInfo">
                    <div className="oprator-ZoonInfo">
                            <p>عملیات</p>

                        </div>
                        <div className="state-ZoonInfo">
                            <p>وضعیت</p>

                        </div>
                        <div className="name-ZoonInfo">
                            <p>نام</p>

                        </div>
                        <div className="number-ZoonInfo">
                            <p>شماره زون</p>

                        </div>
                    </div>
                    <div className="items-ZoonInfo">
                        {
                            props.data.map(item=>{
                                return(
                                    <div className="item-ZoonInfo">
                                        <div className="operator-ZoonInfo" onClick={zoon_alert}>
                                            <div><p>ویرایش</p></div>
                                            
                                            

                                        </div>
                                        
                                        <div className="state-ZoonInfo">
                                            
                                            <div><p>{item.state}</p></div>
                                            

                                        </div>
                                        <div className="name-ZoonInfo">
                                            <p>{item.name}</p>
                                            
                                        </div>

                                         <div className="number-ZoonInfo">
                                            <p>{item.number}</p>
                                            
                                        </div>
                                            

                                    </div>
                                )
                            })
                            
                        }

                    </div>


                </div>

                
            </div>
            </div>
                <div className="add-ZoonInfo">
                    <div className="addIcon-ZoonInfo">

                    </div>

                </div>
                {Alert ? <ZoonAlert close_alert={colse_alert}></ZoonAlert> : ''}
        </div>
    )
}
export default ZoonInfo;