import React from 'react';
import { useState,useEffect } from 'react';
import './App.css';
import Page1 from './components/Page1';
import Setting from './components/Setting';
import Contacts from './components/Contacts';
import Sms from './components/Sms';
import ZoonInfo from './components/ZoonInfo';
import Remotes from './components/Remotes';
import OutPuts from './components/OutPuts';
import Charge from './components/Charge';
import Command from './components/Command'



function App() {
    let [pageState,setPageState]=useState('home');
    let smsList=[{'name':'گروه اول','text':''},{'name':'گروه دوم','text':''},{'name':'گروه سوم','text':''}]
    let zoonList=[{'name':'zone','number':'2','state':'غیر فعال'},{'name':'zone','number':'2','state':'غیر فعال'}]
    let remotesList=[{'name':'ریموت 1','type':'حافظه خالی','number':'ریموت 1','state':'غیر فعال'},{'name':'ریموت 1','type':'حافظه خالی','number':'ریموت 1','state':'غیر فعال'}]
    let outPutsList=[{'name':'خروجي 1','state':'خاموش','key':'o1'},{'name':'خروجي 1','state':'روشن','key':'o2'}];
    const change_page=(pageValue)=>{
    switch (pageValue){
      case 'home':
         setPageState('home')
         break;
         case 'charge':
          setPageState('charge')
          break;
         case 'outPuts':
          setPageState('outPuts')
          console.log('salam')
          break;
        case 'setting':
          setPageState('setting')
          break;
          case 'contact':
            setPageState('contact')
            break;
            case 'sms':
              setPageState('sms')
              break;
              case 'remotes':
                setPageState('remotes')
                break;
              case 'zoonInfo':
                setPageState('zoonInfo')
                break;
          default:
            setPageState('home')
            break;
    }
    return false;
  }

  const [content,setContent]=useState(<Page1 changePage={change_page} ></Page1>);
  
  useEffect(()=>{switch (pageState){
    case 'home':
      setContent(<Page1 changePage={change_page}></Page1>);
    break;
    case 'outPuts':
      setContent(<OutPuts changePage={change_page} data={outPutsList}></OutPuts>);
    break;
    case 'charge':
      setContent(<Charge changePage={change_page} data={'2000'}></Charge>);
    break;
    case 'setting':
      setContent(<Setting changePage={change_page}></Setting>);
      break;
      case 'contact':
        setContent(<Contacts changePage={change_page}></Contacts>);
        break;
        case 'sms':
          setContent(<Sms changePage={change_page} data={smsList}></Sms>);
          break;
          case 'zoonInfo':
            setContent(<ZoonInfo changePage={change_page} data={zoonList}></ZoonInfo>);
            break;
            case 'remotes':
              setContent(<Remotes changePage={change_page} data={remotesList}></Remotes>);
              break;
    default:
      setContent(<Page1 changePage={change_page}></Page1>);
      break;
  }},[pageState])
  return (
    <div className="App">
      {content}
      <Command></Command>


    </div>
  );
}

export default App;
