import React from 'react';
import './Setting.css';
const Setting=(props)=>{
    const go_to_home=()=>{
        props.changePage('home');
    }
    const go_to_contact=()=>{
        props.changePage('contact')
    }

    const go_to_sms=()=>{
        props.changePage('sms')
    }

    const go_to_zoon_info=()=>{
        props.changePage('zoonInfo');
        console.log('go to zoon part')
    }

    const go_to_remotes=()=>{
        props.changePage('remotes');
        console.log('go to remotes part');

    }
    const go_to_out_put=()=>{
        props.changePage('outPuts');
        console.log('go to outPut part');
    }

    const go_to_charge=()=>{
        props.changePage('charge');
        console.log('go to charg part');

    }
    return(
        <div className="Setting">
            <div className="header-Setting">
                <div className="backPart-Setting" onClick={go_to_home}>
                    

                </div>

            </div>
            <div className="menu-Setting">
                <div className='part-Setting' id="part1-Setting">
                    <div className='item-Setting' id='item1-Setting' onClick={go_to_charge}>
                        <div className="icon-Setting">
                            
                        </div>
                        <div className="text-Setting">
                            <p>شارژ سیم کارت</p>

                        </div>

                    </div>
                    <div className='item-Setting' id='item2-Setting' onClick={go_to_sms}>
                        <div className="icon-Setting">
                            
                        </div>
                        <div className="text-Setting">
                            <p>متن پیام ها</p>

                        </div>

                    </div>
                    <div className='item-Setting' id='item3-Setting' onClick={go_to_contact}>
                        <div className="icon-Setting">
                            
                        </div>
                        <div className="text-Setting">
                            <p>شماره تلفن ها</p>

                        </div>

                    </div>
                </div>
                <div className='part-Setting' id="part2-Setting" >
                    <div className='item-Setting' id='item1-Setting' onClick={go_to_out_put}>
                        <div className="icon-Setting">

                        </div>
                        <div className="text-Setting">
                            <p>خروجی ها</p>

                        </div>

                    </div>
                    <div className='item-Setting' id='item2-Setting' onClick={go_to_remotes}>
                        <div className="icon-Setting">

                        </div>
                        <div className="text-Setting" >
                            <p>ریموت ها</p>

                        </div>

                    </div>
                    <div className='item-Setting' id='item3-Setting' onClick={go_to_zoon_info}>
                        <div className="icon-Setting">

                        </div>
                        <div className="text-Setting" >
                            <p>اطلاعات زونها</p>

                        </div>

                    </div>


                </div>
                <div className='part-Setting' id="part3-Setting">
                    <div className='item-Setting' id='item1-Setting'>
                        <div className="icon-Setting">

                        </div>
                        <div className="text-Setting">
                            <p>پشتیبانی فنی</p>

                        </div>

                    </div>
                    <div className='item-Setting' id='item2-Setting'>
                        <div className="icon-Setting">

                        </div>
                        <div className="text-Setting">
                            <p> رمز ورود</p>

                        </div>

                    </div>

                </div>
                


            </div>

        </div>
    )

};

export default Setting;
