import React, { useState } from 'react';
import './SmsPart.css';


const SmsPart=(props)=>{
    const shortHeight='60px';
    const LongHeight='250px';
    const [height,setHeight]=useState('60px');
    const [rotate,setRotate]=useState('rotate(90deg)');
    const open_sms_part=()=>{
        if(height==LongHeight){
            setHeight(shortHeight)
        }else{
            setHeight(LongHeight);
        }
        console.log('close sma editor')
        

    }
    return(
        <div className="SmsPart" style={{height:height}}>
            
            <div id="part1-SmsPart">
                <div className="icon-SmsPart">

                </div>
                <div className="description-SmsPart">
                    <p>{props.name}cscscshcush</p>
                    <p>fejfejfieifjjfeifjiejfiejifjeij</p>
                </div>
                <div className="open-SmartPart" onClick={open_sms_part} style={{transform:rotate}}>

                </div>

            </div>

            <div id="part2-SmsPart">
                <div className="input-SmsPart"  >
                    <input type="text" />
                </div>
                <div className="controllers-SmsPart">
                    <button>ارسال</button>
                    <button>دریافت</button>

                </div>

            </div>
            
            
            {/* <div className="titlPart-SmsPart">
                
                    <p>{props.title}</p>
                
            </div>
            <div className="inputpart-SmsPart">
                <input type="text" />
            </div>
            <div className="controller-SmsPart">
                <div className="send-SmsPart">

                </div>
                <div className="delete-SmsPart">

                </div>
            </div> */}
            

        </div>
    )
}

export default SmsPart;