import React, { useState } from 'react';
import './Command.css';

const Command=()=>{
    const [open,setOpen]=useState(false);
    const [width,setWidth]=useState('90px');
    const [height,setHeight]=useState('90px');

    const oepn_command=()=>{
        setWidth('100vw');
        setHeight('100vh');

    }

    const close_command=()=>{
        setWidth('90px');
        setHeight('90px');

    }




    return(
        <div className="Command" style={{width:width,height:height,fontSize:'large',fontWeight:'600',backgroundColor:width=='90px' ? '':'#009b8ca4'}}>

            <div className="text-Command" style={{visibility: width=='90px' ?'hidden':'visible' }}>
                <div className="titl-Command">
                    <p>پیامک دریافتی دستگاه را وارد کنید</p>
                </div>
                <textarea type="text"  style={{fontSize:'medium',fontWeight:'600'}}></textarea>
                <button style={{fontSize:'large',fontWeight:'600'}}>اعمال</button>
                <button onClick={close_command} style={{fontSize:'large',fontWeight:'600'}}>انصراف</button>

            </div>
            <div className="icon-Command" onClick={oepn_command} >

            </div>


        </div>
    )
}

export default Command;